import {useState} from "react";
import ReactApexChart from "react-apexcharts"

export function LineChartComponent(/*props*/):JSX.Element {

    const [chartData] = useState(
        {

            series: [{
                data: [400, 430, 448, 470, 540, 580, 690, 1100, 1200, 1380]
            }],
            options: {
                chart: {
                    type: 'bar',
                    height: 350
                },
                plotOptions: {
                    bar: {
                        borderRadius: 4,
                        horizontal: true,
                    }
                },
                dataLabels: {
                    enabled: false
                },
                xaxis: {
                    categories: ['South Korea', 'Canada', 'United Kingdom', 'Netherlands', 'Italy', 'France', 'Japan',
                        'United States', 'China', 'Germany'
                    ],
                }
            },


        }
    );

    return (
            <>
                <ReactApexChart options={chartData} series={chartData.series} type="bar" height={350} />
            </>
        );
}

export function PieChartComponent() {
    const [chartData] = useState(
        {

            series: [44, 55, 13, 43, 22],
            options: {
                chart: {
                    width: 380,
                    type: 'pie',
                },
                labels: ['Team A', 'Team B', 'Team C', 'Team D', 'Team E'],
                responsive: [{
                    breakpoint: 480,
                    options: {
                        chart: {
                            width: 200
                        },
                        legend: {
                            position: 'bottom'
                        }
                    }
                }]
            },


        }
    )

    return (
        <>
            <ReactApexChart options={chartData} series={chartData.series} type={"pie"} width={380}></ReactApexChart>
        </>
    )
}

